.fadedown {
  animation-name:fadedown;
  animation-duration:2s;
  animation-iteration-count: 1;
  animation-delay: 2.5s;
}

@keyframes fadedown{
  to{
    opacity: 0;
    bottom: 5%;
  }
  from{
    opacity: 1;
    bottom: 15%;
  }
}