html,body {
  font-family: 'Noto Sans TC';
  font-size: 16px;
  color: $light;
}
.loading {
  position: fixed;
  left: 0px;
  top: 0px;
  width: 100%;
  height: 100%;
  z-index: 9999;
  background:black url(../images/Ripple-1s-200px.svg) center no-repeat;
  background-size: 100px;
}
.rounded-big {
  border-radius: 100px;
}
@mixin bg-cover {
  background-size: cover;
  background-position: center center;
}
@mixin clearfix() {
  &::after {
    display: block;
    content: "";
    clear: both;
  }
}
.clearfix {
  @include clearfix;
}
.w-md-50 {
  @include media-breakpoint-up(md){
    width: 50%;
  }
}
.bg {
  background-size: cover;
  background-position: center;
  &-1 {
    background-image: url(../images/bg-1.jpg);
  }
  &-2 {
    background-image: url(../images/bg-2.jpg);
  }
  &-3 {
    background-image: url(../images/bg-3.jpg);
  }
  &-4 {
    background-image: url(../images/bg-4.jpg);
  }
}
.bg-fb {
  background-color: #385898;
}
.header {
  height:100vh; //calc(100vh + 50px)
  width: 100%;
  background-image: url(../images/bg.jpg);
  background-position: center bottom;
  background-size: cover;
  position: relative;
  overflow: hidden;
}
.snow {
  position: absolute;
  left: 0;
  top:0;
  width: 100%;
  height: 100%;
  z-index: 0;
  // background: #000;
}
/*a*/.logo {
  position: absolute;
  margin: auto;
  left: 0;
  right: 0;
  bottom: 90px;
  background: url(../images/logo.png) center bottom no-repeat;
  background-size: 85% auto;
  height:50%;
  width: 100%;
  max-width: 520px;
  z-index: 10;
  transition: all .3s;
  opacity: 0;
  &:hover {
    background-image: url(../images/logo_hover.png);
  }
  @include media-breakpoint-up(md){
    background-size: auto 100%;
    max-height: 275px;
    bottom: 120px;
  }
  @media (max-height:480px) {
    background-size: auto 85%;
  }
}
.downlaod {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: flex-end;
  bottom: 1em;
  opacity: 0;
}
.btn-download {
  width: 45%;
  height: 60px;
  max-width: 300px;
  display: block;
  background: #000 center no-repeat;
  background-size: 60% auto;
  border-radius: 10px;
  border: 2px solid white;
  margin-left: 1em;
  transition: all .3s;
  &-google {
    background-image: url(../images/google-play.png);
  }
  &-ios {
    background-image: url(../images/app-store.png);
  }
  &:hover {
    transform: scale(1.1);
  }
  &:first-child {
    margin-left: 0;
  }
  @include media-breakpoint-up(md) {
    height: 75px;
  }
}
.btnGroup {
  position: absolute;
  right: .5em;
  height: 100vh;
  display: flex;
  flex-direction: column;
  // justify-content: center;
  // align-items: center;
  z-index: 100;
}
.p {
  position: absolute;
  top: -5%;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  z-index: 0;
  &-left {
    left: -35%;
    background-size: auto 60%;
    background-image: url(../images/left.png);
    background-position: right center;
    transform: translateX(-100%);
  }
  &-right {
    right: -40%;
    background-size: auto 55%;
    background-image: url(../images/right.png);
    background-position: left center;
    transform: translateX(100%);
  }
  @include media-breakpoint-up(sm) {
    top: 0;
    &-left {
      left: -30%;
      background-position: left center;
    }
    &-right {
      right: -30%;
      background-position: right center;
    }
  }
  @include media-breakpoint-up(md) {
    &-left {
      left: 0;
      background-size: 52.5% auto;
    }
    &-right {
      right: 0;
      background-size: 48% auto;
    }
  }
  @media (max-height:480px) {
    &-left {
      left: 0;
      background-position: left center;
      background-size: 45% auto;
    }
    &-right {
      left: 0;
      background-position: right center;
      background-size: 40% auto;
    }
  }
}
.btn-circle {
  width: 50px;
  height: 50px;
  border-radius: 100px;
  border: 1px solid rgba(255, 255, 255, 0.6);
  background-position: center;
  background-repeat: no-repeat;
  margin-top: .5em;
  opacity: 0.75;
  position: relative;
  transition: transform .3s;
  &:hover{
    opacity: 1;
    transform: scale(1.1);
  }
  @include media-breakpoint-up(md){
    width: 70px;
    height: 70px;
  }
  
}
.icon-play {
  background-color: black;
  background-image: url(../images/play.svg);
}
.icon-fb {
  background-color: #385898;
  background-image: url(../images/fb.svg);
  background-size: auto 60%;
}
.circle-animation {
  animation: circle 5s infinite linear;
}
.content {
  padding: 1em;
  background: rgba(black,.75);
  box-shadow: 0 0 20px 2px $blue;
  max-width: 750px;
  border-radius: 10px;
  &-red {
    box-shadow: 0 0 20px 2px $red;
  }
  &-purple {
    box-shadow: 0 0 20px 2px $purple;
  }
  &-yellow {
    box-shadow: 0 0 20px 2px $yellow;
  }
}
.tit-text {
  text-align: center;
  position: relative;
  font-weight: bold;
  * {
    font-weight: bold;
  }
}
.text-shadow {
  &-blue {
    text-shadow: 2px  2px 1px darken($blue,30), 0  0 10px $blue, 0  0 20px $blue;
  }
  &-red {
    text-shadow: 2px  2px 1px darken($red,30), 0  0 10px $red, 0  0 20px $red;
  }
  &-purple {
    text-shadow: 2px  2px 1px darken($purple,30), 0  0 10px $purple, 0  0 20px $purple;
  }
  &-yellow {
    text-shadow: 2px  2px 1px darken($yellow,30), 0  0 10px $yellow, 0  0 20px $yellow;
  }
}
.tit-p {
  text-align: center;
  position: relative;
  color: white;
  &::before {
    content: '';
    position: absolute;
    margin: auto;
    top: 0;
    bottom: 0;
    height: 1px;
    width: 50px;
    background-color: white;
    transform: translateX(-120%);
  }
  &::after {
    content: '';
    position: absolute;
    margin: auto;
    top: 0;
    bottom: 0;
    height: 1px;
    width: 50px;
    background-color: white;
    transform: translateX(20%);
  }
}
.fb-like {
  &::before {
    content: '&';
    margin-right: 0.5em;
    color: $white;
  }
}
.gift {
  list-style: none;
  text-align: center;
  padding-left: 0;
  margin-top: 2em;
  li {
    text-align: center; 
    margin-left: .5em;
    &:first-child {
      margin-left: 0;
    }
  }
}
.gift-icon {
  border: 2px solid $primary;
  border-radius:3px;
  overflow: hidden;
  max-width: 100%;
  height: auto;
  display: inline-block;
  &-red {
    border-color: $red;
  }
  @include media-breakpoint-up(sm) {
    border-width: 3px;
    border-radius: 10px;
  }
}
.page-p {
  display: none;
  @include media-breakpoint-up(md) {
    left: -30%;
    display: block;
    width: 100%;
    height: 100%;
    background:url(../images/page-p.png) left center no-repeat;
    background-size: 100% auto;
  }
  @include media-breakpoint-up(lg) {
    background-size: 80% auto;
    left:-20%;
  }
} 
.num {
  text-align: center;
  margin-bottom: 1.5em;
  margin-top: 1.5em;
  >span {
    font-family: 'Century Gothic', serif;
    font-size: 1.7em;
    color: white;
    padding: .5em;
    background-color: darken($red,20);
    border: 1px solid red;
    // border-radius: 20px;
  }
  @include media-breakpoint-up(sm) {
    margin-top: 3em;
    margin-bottom:3em;
    >span {
      font-size: 3em;
    }
  }
  @include media-breakpoint-up(lg) {
    margin-top: 4em;
    margin-bottom:5em;
    >span {
      font-size: 5em;
    }
  }
}
.progress {
  width: 95%;
}
.progress-bg {
  border: 2px solid $red;
  background:rgba($color:$white, $alpha: .5); 
  height:14px; 
  overflow: hidden;
}
.progress-bar {
  height:10px; 
  width: 0%;
  background-color: darken($red,20);
  transition: width 4.50s ease !important;
}
.gift-progress {
  width: 100%;
  position: relative;
  list-style: none;
  li {
    position: absolute;
    transform: translateX(-50%);
    width: 9%;
    text-align: center;
    margin-top: 1em;
    opacity: .6;
    filter: grayscale(1);
    
    &:nth-child(1){
      left: 10%;
      &::after {
        content: '5千';
      }
    }
    &:nth-child(2){
      left: 20%;
      &::after {
        content: '1萬';
      }
    }
    &:nth-child(3){
      left: 40%;
      &::after {
        content: '2萬';
      }
    }
    &:nth-child(4){
      left:100%;
      &::after {
        content: '5萬';
      }
    }
    &::before {
      position: absolute;
      margin: auto;
      left: 0;
      right: 0;
      top: -30px;
      content: '';
      height: 30px;
      width: 2px;
      background-color:$red;
    }
    &::after {
      position: absolute;
      margin: auto;
      left: 0;
      right: 0;
      top:-50px;
      font-size: 0.8em;
    }
    &.on {
      opacity: 1;
      filter: grayscale(0);
    }
    @include media-breakpoint-up(sm) {
      &:nth-child(1){
        &::after {
          content: '5千人';
        }
      }
      &:nth-child(2){
        &::after {
          content: '1萬人';
        }
      }
      &:nth-child(3){
        &::after {
          content: '2萬人';
        }
      }
      &:nth-child(4){
        &::after {
          content: '5萬人';
        }
      }
    }

    small {
      display: block;
    }
  }
}
.card {
  flex-flow: column;
  color: $light;
  li {
    list-style: none;
    margin-top: 1.5em;
  }
  .d-flex {
    padding-top: 1em;
    padding-bottom: 1em;
    border-bottom: 1px solid rgba($secondary,.5);
    border-top: 1px solid rgba($secondary,.5);
    img {
      width: auto;
    }
  }
  .btn {
    border-radius: 5px;
  }
}
.card-option {
  >div {
    position: relative;
    height: 110px;
    max-width: 80px;
    margin-left: auto;
    margin-right: auto;
  }
  &:nth-last-child(n+4){
    margin-bottom: 1em;
  }
  img {
    position: absolute;
    left: 0;
    &:nth-child(2) {
      // display: none;
      opacity: 0;
    }
  }
  @include media-breakpoint-up(sm) {
    div {
      height: 160px;
      max-width: 75%;
    }
  }
  @include media-breakpoint-up(md) {
    div {
      height: 260px;
      max-width: 85%;
    }
  }
  @include media-breakpoint-up(lg) {
    div {
      height: 200px;
      max-width: 90%;
    }
  }
  @include media-breakpoint-up(xl) {
    div {
      height: 250px;
    }
  }
}
.btn-start {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(darken($purple,40),.5);
  border-radius: 10px;
  z-index: 100;
  display: flex;
  justify-content: center;
  align-items: center;
  a {
    background-color: $purple;
    color: white;
    border-radius: 5px;
    font-size: 1.5em;
    box-shadow: 0px 3px 0px 3px darken($purple, 20);
    padding: .5em 1.5em;
    transition: all .3s;
    &:hover {
      box-shadow: 0px 3px 0px 3px darken($purple, 20),0 0 20px 3px $purple;
      transform: scale(1.05);
    }
  }
}
.carousel {
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 0 20px 2px $yellow;
}


/**
*footer區塊內容
**/
footer {
  background-color:darken($color: #390b0b, $amount:5);
}
/*注意事項*/
.notice {
  background:#390b0b url(../images/bg-r.png) center bottom repeat;
  padding: 1em;
  line-height: 1.5em;
  ol {
    padding-left:2em;
    font-size: 0.95em;
  }
  a {
    word-break: break-all;
  }
}
.company {
  text-align: left;
  margin-top: 10px;
  font-size: .5em;
  li {
    margin-right: 15px;
  }
}
.copyright {
  font-size: 0.8em;
}
.icon-gameboss {
  background-image: url(../images/gameboss.png);
  width: 42px;
  height: 42px;
}
.icon-dd {
  background-image: url(../images/DD.png);
  width: 56px;
  height: 23px;
}
.icon {
  margin-left: 10px;
  span {
    width: 42px;
    height: 42px;
    display: block;
    background-repeat: no-repeat;
  }
  &-12 {
    background-image: url(../images/12.png);
  }
  &-15 {
    background-image: url(../images/15.png);
  }
}
.grade {
  list-style-type: none;
  line-height: 1.3em;
  font-size: 0.8em;
}

.modal-content {
  border-radius: 10px;
  border: 1px solid $primary;
  background-color: rgba(255, 255, 255, 0.75);
  box-shadow: 0 0 10px 2px $primary;
  position: relative;
}
.close.close {
  position: absolute;
  top: -1.5em;
  left: 50%;
  transform: translateX(-50%);
  filter: drop-shadow(0 0 5px $primary) drop-shadow(0 0 5px $primary);
  color: #fff;
  opacity: 0.7;
  z-index: 201;
  @include media-breakpoint-up(md) {
    left: auto;
    right: 0;
    transform: none;
  }
}
.close:not(:disabled):not(.disabled):hover {
  color: #fff;
  opacity: 1;
}

@keyframes circle {
  0% {
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
